/*################################*\
    legacy  |  _c-SupporterBlock.less
\*################################*/
.c-SupporterBlock {
  margin-bottom: 3rem;
  margin-bottom: calc(2 * var(--lh));
  margin-bottom: 2lh;
  --supporter-logo-width: var(--supporter-logo-sml-width);
  --supporter-logo-height: var(--supporter-logo-sml-height);
  text-align: center;
}
@supports (margin-block-end: var(--v)) {
  .c-SupporterBlock {
    margin-bottom: unset;
    margin-block-end: calc(2 * var(--lh));
    margin-block-end: 2lh;
  }
}
.c-SupporterBlock__Hn {
  font-size: calc(var(--font-scale, 1) * 1em);
  line-height: calc(var(--line-height) / var(--font-scale, 1) * var(--tracks, 1));
  --font-scale: 1.75;
  font-family: inherit;
  font-weight: 900;
  color: #333;
}
@media (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .c-SupporterBlock__Hn {
    font-size: 1.75em;
    line-height: 0.85714286;
  }
}
@supports (flex-wrap: wrap) {
  .c-SupporterBlock__Hn {
    display: flex;
    align-items: center;
  }
  .c-SupporterBlock__Hn::before,
  .c-SupporterBlock__Hn::after {
    content: '';
    flex: 1;
    height: 0.1875rem;
    height: calc(0.125 * var(--lh));
    height: 0.125lh;
    margin: 0 1rem;
    background-image: linear-gradient(to right, transparent, #e8eaeb, transparent);
    background-image: linear-gradient(var(--direction), #e8eaeb, transparent);
  }
  @supports (block-size: var(--v)) {
    .c-SupporterBlock__Hn::before,
    .c-SupporterBlock__Hn::after {
      height: unset;
      block-size: calc(0.125 * var(--lh));
      block-size: 0.125lh;
    }
  }
  .c-SupporterBlock__Hn::before {
    --direction: to left;
  }
  @supports (background-image: linear-gradient(to inline-start, transparent, black) {
    .c-SupporterBlock__Hn::before {
      --direction: to inline-start;
    }
  }
  .c-SupporterBlock__Hn::after {
    --direction: to right;
  }
  @supports (background-image: linear-gradient(to inline-end, transparent, black) {
    .c-SupporterBlock__Hn::after {
      --direction: to inline-end;
    }
  }
}
.c-SupporterBlock__List {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.c-SupporterBlock__List__Item {
  flex: 0 1 var(--supporter-logo-width);
  margin-bottom: 1.5rem;
  margin-bottom: var(--lh);
  margin-bottom: 1lh;
  margin-left: 1rem;
  margin-right: 1rem;
}
@supports (margin-block-end: var(--v)) {
  .c-SupporterBlock__List__Item {
    margin-bottom: unset;
    margin-block-end: var(--lh);
    margin-block-end: 1lh;
  }
}
@supports (margin-inline: 1rem) {
  .c-SupporterBlock__List__Item {
    margin-left: unset;
    margin-right: unset;
    margin-inline: 1rem;
  }
}
.c-SupporterBlock__List__Item.medium {
  --supporter-logo-width: var(--supporter-logo-med-width);
  --supporter-logo-height: var(--supporter-logo-med-height);
}
.c-SupporterBlock__List__Item.large {
  --supporter-logo-width: var(--supporter-logo-lrg-width);
  --supporter-logo-height: var(--supporter-logo-lrg-height);
}
.c-SupporterBlock__Logo {
  max-height: var(--supporter-logo-height);
  transition: filter 1000ms;
}
.c-SupporterBlock__Logo:not(:hover):not(:focus) {
  filter: grayscale(100%);
}
.c-SupporterBlock__Label {
  font-size: calc(var(--font-scale, 1) * 1em);
  line-height: calc(var(--line-height) / var(--font-scale, 1) * var(--tracks, 1));
  --font-scale: 0.75;
  text-transform: uppercase;
  color: var(--color-secondary, #6fc2c4);
}
@media (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .c-SupporterBlock__Label {
    font-size: 0.75em;
    line-height: 2;
  }
}
